import React, {ChangeEvent, useState} from 'react';

type SearchUserType = {
    searchCallback: (searchUser: string) => void
    errorFilterUser: string
    subscriberCallback: (friend:boolean) => void
}

export const SearchUser: React.FC<SearchUserType> = ({
                                                         searchCallback, errorFilterUser,
                                                         subscriberCallback, ...props
                                                     }) => {


    const [searchUser, setSearchUser] = useState('')

    const onChangeSearch = (e: ChangeEvent<HTMLInputElement>) => {
        setSearchUser(e.currentTarget.value)
    }

    const Search = () => {
        searchCallback(searchUser)
    }

    const Subscriber = (friend:boolean) => {
        subscriberCallback(friend)
    }

    return (
        <div>
            <input onChange={onChangeSearch} placeholder={'Search User on Name'}/>
            {errorFilterUser && <div>{errorFilterUser}</div>}
            <button onClick={Search}>Search</button>
            <div>
                <button onClick={()=>Subscriber(false)}>AllUsers</button>
                <button onClick={()=>Subscriber(true)}>Subscribers  Users</button>
            </div>
        </div>
    );
};

