import axios, {AxiosResponse} from "axios";

const instance = axios.create(
    {
        baseURL: `https://social-network.samuraijs.com/api/1.0`,
        withCredentials: true,
    }
)

export const userAPI = {

    getUser(currentPage:number,pageSize:number) {

        const promise = instance.get<UserType>(
            `/users?page=${currentPage}&count=${pageSize}`)
        return promise
    },

    getUserByName(currentPage:number,pageSize:number,searchUser:string) {

        const promise = instance.get<UserType>(
            `/users?page=${currentPage}&count=${pageSize}&term=${searchUser}`)
        return promise
    },
    getUserFriend(currentPage:number,pageSize:number,friend:boolean) {

        const promise = instance.get<UserType>(
            `/users?page=${currentPage}&count=${pageSize}&friend=${friend}`)
        return promise
    },


    getChangePageUser(p:number,pageSize:number) {
        const promise = instance.get<UserType>(
            `/users?page=${p}&count=${pageSize}`)
        return promise
    },



    Follow(userId:number) {
        const promise = instance.post<{}, AxiosResponse, {userId: number}>(
            `/follow/${userId}`,{userId}
        )
        return promise
    },

    UNFollow(userId:number) {
        const promise = instance.delete(
            `/follow/${userId}`,
        )
        return promise
    },

}


export type UserType = {

    items: ItemsType[]
    totalCount: number,
    error: null

}

export type ItemsType = {

    name: string,
    id: number,
    photos: {
        small: null,
        large: null
    },
    status: string,
    followed: boolean

}


