import React from 'react';
import {connect} from "react-redux";
import {AppDispatchType, StoreType,} from "../../Redux/redux-store";

import {
    DisableType,
    ThunkChangePage, ThunkFollow, ThunkSubscriber, ThunkUNFollow, ThunkUser, ThunkUserFilter,

} from "../../Redux/userReducer";
import {ItemsType} from "../../API/User-api";
import User from "./User";
import withAuthRedirect from "../Hoc/WithAuthRedirect";
import {compose} from "redux";


class UserWrapper extends React.Component<UsersType> {


    componentDidMount() {

        this.props.GetUser(this.props.currentPage, this.props.pageSize)

    }


    render() {


        const ChangePage = (p: number) => {

            this.props.ChangePageUser(p, this.props.pageSize)
        }

        const Follow = (userId: number) => {

            this.props.Follow(userId)
        }

        const UNFollow = (userId: number) => {

            this.props.UNFollow(userId)

        }

        const SearchCallback = (searchUser: string) => {
            this.props.searchCallback(this.props.currentPage,this.props.pageSize,searchUser)
        }


        const SubscriberCallback=(friend:boolean)=> {
            this.props.subscriberCallback(this.props.currentPage,this.props.pageSize,friend)
        }


        return (


            <User changePage={ChangePage}
                  currentPage={this.props.currentPage}
                  pageSize={this.props.pageSize}
                  totalUsersCounter={this.props.totalUsersCounter}
                  users={this.props.users}
                  isFetching={this.props.isFetching}
                  follow={Follow}
                  unfollow={UNFollow}
                  disable={this.props.disabled}
                  searchCallback={SearchCallback}
                  errorFilterUser={this.props.errorFilterUser}
                  subscriberCallback={SubscriberCallback}


            />


        );
    }
}


type mapStateToPropsType = {
    users: ItemsType[]
    pageSize: number
    totalUsersCounter: number
    currentPage: number
    isFetching: boolean
    disabled: DisableType
    errorFilterUser:string


}

const mapStateToProps = (state: StoreType): mapStateToPropsType => {


    return {
        users: state.userReducer.users,
        pageSize: state.userReducer.pageSize,
        totalUsersCounter: state.userReducer.totalUsersCounter,
        currentPage: state.userReducer.currentPage,
        isFetching: state.userReducer.isFetching,
        disabled: state.userReducer.disable,
        errorFilterUser:state.userReducer.errorFilterUser


    }
}

type mapDispatchToPropsType = {
    Follow: (id: number) => void
    UNFollow: (id: number) => void
    GetUser: (p: number, pageSize: number) => void
    ChangePageUser: (currentPage: number, pageSize: number) => void
    searchCallback: (p: number, pageSize: number, searchUser: string) => void
    subscriberCallback:(p: number, pageSize: number,friend:boolean)=>void


}

export type UsersType = mapDispatchToPropsType & mapStateToPropsType


const mapDispatchToProps = (dispatch: AppDispatchType): mapDispatchToPropsType => {


    return {
        Follow: (id: number) => {
            dispatch(ThunkFollow(id))

        },
        UNFollow: (id: number) => {
            dispatch(ThunkUNFollow(id))
        },

        searchCallback: (p: number, pageSize: number, searchUser: string) => {
            dispatch(ThunkUserFilter(p, pageSize, searchUser))

        },
        GetUser: (p: number, pageSize: number) => {
            dispatch(ThunkUser(p, pageSize))
        },
        ChangePageUser: (pageNumber: number, pageSize: number) => {
            dispatch(ThunkChangePage(pageNumber, pageSize))
        },

        subscriberCallback:(p: number, pageSize: number,friend:boolean)=>{
            dispatch(ThunkSubscriber(p, pageSize,friend))
        }


    }
}


export default compose<React.ComponentType>(
    connect(mapStateToProps, mapDispatchToProps),
    withAuthRedirect)(UserWrapper)

